// switch-track å®ä¹å¼å³åæ¢è¿å¨è½¨è¿¹ï¼é¿åº¦è¦å switch ä¿æä¸è´ï¼éè¦ä¼ªç±» ::before
// switch-knob ç¨æ¥å®ä¹å¼å³åæ¢çº½æ£ï¼ä¼ªç±» ::before ç¨æ¥å®ä¹èç¦ææ
.switch {
  position: relative;
  display: inline-block;

  // ç¨æ¥æ§å¶å¼å³ç input type="checkbox" ç»ä»¶
  &-control {
    z-index: 2;
    display: inline-block;
    opacity: 0;
    transition: switch-transition(transform);
    cursor: pointer;

    @include switch-control-size;

    // Track å½å¼å³åæ¢æ¶ï¼æ¾ç¤ºçè½¨è¿¹
    &-track {
      position: relative;
      display: block;
      outline: none;
      user-select: none;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: .38;
        transition: switch-transition(opacity), switch-transition(background-color);
      }

      @include switch-track-size;
    }

    &-knob {
      position: absolute;
      z-index: 1;
      display: block;
      transform: translateX(0);
      transition: switch-transition(transform), switch-transition(background-color);

      @include shadow(2);
      @include switch-knob-size;
    }

    // éä¸­æ¶
    &:checked ~ .switch-track {
      //// Track
      //&::before {
      //  opacity: .5;
      //}

      .switch-knob {

        // Focus indicator
        //&::before {
        //  opacity: .15;
        //}
      }
    }

    // disabled
    &:disabled {
      cursor: initial;

      ~ .switch-track {
        // Track
        &::before {
          opacity: .12;
        }

        .switch-knob {
          background-color: $switch-disabled-knob-color;
        }
      }
    }
  }

  // èç¦ææ
  &-focus-ring {
    .switch-knob {
      &::before {
        content: "";
        opacity: .2;
        transform: scale(0);
        transition: switch-transition(transform), switch-transition(background-color);

        @include switch-focus-ring-size;
      }
    }

    // è·åç¦ç¹æ¶
    .switch-control:focus ~ .switch-track {
      .switch-knob {
        &::before {
          transform: scale(1);
        }
      }
    }
  }

  // æ²¡æéä¸­æ¶èæ¯é¢è²
  @include switch-unchecked-bg-color($switch-unchecked-track-color, $switch-unchecked-knob-color, $switch-unchecked-focus-ring-color);

  // éä¸­æ¶çèæ¯é¢è²
  @include switch-checked-bg-color($switch-baseline-theme-color);
}

// å®ä¹ä¸»é¢æ ·å¼
@if ($enable-custom-theme and $enable-custom-theme-for-switch) {
  // ä¸»é¢è²
  @each $theme in ("primary", "secondary") {
    .switch-#{$theme} {
      @include switch-theme($theme);
    }
  }

  // è¾å©é¢è²å¯¹åº switch
  @each $theme in $theme-assist {
    .switch-#{$theme} {
      @include switch-theme($theme);
    }
  }
}
